import React, { useState, useEffect } from "react"
import store from "store2"
import "./styles.css"
import { FormGroup, TextField, Typography, Grid } from "@material-ui/core"
import { useDebounce } from "use-debounce"
import urlRegex from "url-regex"
import queryString from "query-string"
import objectAssignDefined from "object-assign-defined"
import LinkIcon from "@material-ui/icons/Link"
import { WindowLocation as WLocation } from "@reach/router"

export type Config = {
  server: string
  apiKey: string
}

type Props = {
  onConfigChanged: Function
  location?: WLocation
  config?: Config
}
export const OctostatusConfigForm = (props: Props) => {
  const debounceDuration = 1000
  const localStorageKeyConfig = "lsk_config"

  const parsed = queryString.parse(props.location?.search || "") as Config

  const [config, setConfig] = useState<Config>(
    objectAssignDefined(
      store.get(localStorageKeyConfig, {
        server: props.config?.server || "",
        apiKey: props.config?.apiKey || "",
      }),
      { server: parsed.server, apiKey: parsed.apiKey }
    )
  )
  useEffect(() => {
    store.add(localStorageKeyConfig, config)
  }, [config])

  const [debouncedConfig] = useDebounce(config, debounceDuration)
  useEffect(() => {
    props.onConfigChanged(debouncedConfig)
  }, [debouncedConfig])

  const permalink = (): string => {
    console.dir(props.location)
    return `${props.location?.origin}${props.location?.pathname}?server=${config.server}&apiKey=${config.apiKey}`
  }
  return (
    <form noValidate autoComplete="off">
      <FormGroup row>
        <TextField
          label="Server"
          variant="outlined"
          required
          fullWidth
          margin="dense"
          error={!urlRegex({ exact: true }).test(config.server)}
          onChange={e => {
            setConfig({
              ...config,
              server: e.target.value,
            })
          }}
          placeholder="e.g. http://octopi.local"
          value={config.server}
        />
        <TextField
          label="API Key"
          variant="outlined"
          fullWidth
          margin="dense"
          onChange={e => {
            setConfig({
              ...config,
              apiKey: e.target.value,
            })
          }}
          placeholder="Copy from Octopi (make sure to enable CORS)"
          value={config.apiKey}
        />
      </FormGroup>
      <Grid container justify="flex-end">
        <Typography variant="subtitle2" align="right">
          <a href={permalink()}>
            Permalink <LinkIcon style={{ width: "24px", height: "24px", marginBottom: "-6px" }} />
          </a>
        </Typography>
      </Grid>
    </form>
  )
}
